*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.yo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 850px;
  width: 90%;
  margin: 0 auto;
  font-family: sans-serif;
  padding: 4rem;
  background-color: #F2F7F8;
}

h1 {
  margin-bottom: 1rem;
}

.button {
  margin-top: 1.5rem;
  padding: 1rem 1.2rem;
  border: none;
  background: #f9032f;
  color: #fff;
  text-decoration: none;
  font-size: 1rem;
  border-radius: 2rem;
  cursor: pointer;
  outline: none;
  font-size: 1.2rem;
}

.button:hover {
  background: #00bcd4;
  color: #fff;
}